@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.eot?33dl1i");
  src: url("../fonts/icomoon.eot?33dl1i#iefix") format("embedded-opentype"),
    url("../fonts/icomoon.ttf?33dl1i") format("truetype"),
    url("../fonts/icomoon.woff?33dl1i") format("woff"),
    url("../fonts/icomoon.svg?33dl1i#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /*  use !important to prevent issues with 
   browser extensions that change fonts  */
  font-family: "icomoon", serif !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

